<template>
    <div class="row">
        <div class="col m8">
            <div id="dashboard">
                <ul class="collection with-header">
                    <li class="collection-header">
                        <h4>Employees</h4>
                    </li>
                    <li class="collection-item" v-for="employee in employess" :key="employee.id">
                        <div class="chip">{{employee.dept}}</div>
                        {{employee.employee_id}} : {{employee.name}}

                        <router-link class="secondary-content"
                            v-bind:to="{name:'view_employee', params:{id:employee.employee_id}}">
                            <i class="fa fa-eye"></i>
                        </router-link>
                    </li>
                </ul>
                
                <div class="fixed-action-btn">
                    <router-link to="/NewEmployee" class="btn-floating btn-large red">
                        <i class="fa fa-plus"></i>
                    </router-link>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
    import Firebaseinit from '../firebaseinit'
    export default {
        name: 'Dashboard',
        data() {
            return {
                employess: []
            }
        },
        created() {
            Firebaseinit.collection('1').orderBy('dept').get().then(queryselector => {
                queryselector.forEach(doc => {
                    const data = {
                        id: doc.id,
                        employee_id: doc.data().employee_id,
                        name: doc.data().name,
                        dept: doc.data().dept,
                        position: doc.data().position
                    }
                    this.employess.push(data);
                });
            });
        }
    }
</script>

<style lang="scss">
    #dashboard {
        margin-top: 4rem;

        .collection {
            border: 1px solid #f39c12;

            .collection-item {
                border-bottom: 1px solid #f39c12;
                ;
            }
        }
        .fa-eye{
            color: #f39c12;
        }
    }
</style>