<template>
    <div id="view">
        <h2>view employees</h2>
        <div class="row">
            <div class="col s12 m6">
                <div class="card">
                    <div class="card-content white-text">
                        <h3 class="card-title">{{name}}</h3>
                        <p>I am a very simple card. I am good at containing small bits of information.
                            I am convenient because I require little markup to use effectively.</p>
                    </div>
                    <div class="card-action">
                        <span>{{position}}</span>
                        <span>{{dept}}</span>
                    </div>
                </div>
            </div>
        </div>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteEmployee" class="btn red">Delete</button>
        <div class="fixed-action-btn">
                   <router-link 
                    v-bind:to="{name:'edit_employee',params:{employee_id:employee_id}}" 
                    class="btn-floating btn-large red">
                        <i class="fa fa-pencil"></i>
                    </router-link>
                </div>
    </div>
</template>

<script>
    import Firebaseinit from '../firebaseinit'
    export default {
        name: 'view',
        data() {
            return {
                employee_id: null,
                name: null,
                dept: null,
                position: null
            }
        },
        beforeRouteEnter(to, from, next) {
            Firebaseinit.collection('1').where('employee_id', '==', to.params.id).get().
            then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                            vm.employee_id = doc.data().employee_id,
                            vm.name = doc.data().name,
                            vm.dept = doc.data().dept,
                            vm.position = doc.data().position
                    })
                })
            })
        },
        watch: {
            '$route': 'theData'
        },
        methods: {
            theData() {
                Firebaseinit.collection('1').where('employee_id', '==', this.$route.params.id).get().
                then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                            this.temployee_id = doc.data().employee_id,
                            this.name = doc.data().name,
                            this.dept = doc.data().dept,
                            this.position = doc.data().position
                    })
                })
            },
            deleteEmployee() {
                if (confirm('Are you sure')) {
                    Firebaseinit.collection('1').where('employee_id', '==', this.$route.params.id).get().
                    then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                            this.$router.push('/')
                        })
                    })
                }
            }
        }
    }
</script>

<style lang="scss">
    .card {
        background-color: #dfe6e9 !important;
        border: 1px solid #d35400;
        cursor: pointer;
        padding: 4px;

        .card-content {
            p {
                color: #000;
            }
        }

        .card-title {
            font-weight: bold !important;
            color: #d35400;
        }

        .card-action {
            text-align: center;

            span {
                font-size: 17px;
                font-weight: bold;
                margin-left: 15px;
                color: #d35400;
            }
        }
    }

    .btn {
        margin-left: 10px;
    }
</style>