<template>
    <div id="ourlogin">
        <div class="container">
            <div class="row">
                <div class="col m8 s12 offset-m2">
                    <div class="login card-panel  white-text center">
                        <h3>Register</h3>
                        <form>
                            <div class="input-field">
                                <input type="text" id="email" placeholder="Email" v-model="email"> 
                            </div>
                            <div class="input-field">
                                <input type="text" id="password" placeholder="password" v-model="password">
                            </div>
                        </form>
                        <button type="submit" @click="register" class="btn btn-large grey lighten-4 black-text">Register</button>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
import firebase from 'firebase';
    export default {
        name:'Register',
        data(){
            return{
                email:'',
                password:''
            }
        },
        methods:{
            register(e){
                firebase.auth().createUserWithEmailAndPassword(this.email, this.password).
                then(user =>{
                    alert(`your account for ${user.email}`)
                    this.$router.push('/')
                }).catch(error=>{
                    console.log(error)
                })
                e.preventDefault();
                
            }
        }
    }
</script>


<style lang="scss">
    .card-panel {
        margin-top: 50px !important;
        background: #f39c12 !important;
        .input-field {
            input[type=text]::placeholder{
                color: #fff;  
                  
            }
        }
    }
</style>