<template>
    <div id="edit">
        <h2>Edit employee</h2>
        <div class="row">
            <form @submit.prevent="udataEmployee" class="col m8 s12">
                <div class="row">
                    <div class="input-field col m8 s12">
                        <input type="text" v-model="employee_id" placeholder="Employee ID#" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col m8 s12">
                        <input type="text" v-model="name" placeholder="Name" required>

                    </div>
                </div>
                <div class="row">
                    <div class="input-field col m8 s12">
                        <input type="text" v-model="dept" placeholder="Deptrment" required>

                    </div>
                </div>
                <div class="row">
                    <div class="input-field col m8 s12">
                        <input type="text" v-model="position" placeholder="position" required>

                    </div>
                </div>
                <button type="submit" class="btn">Updata</button>
                <router-link to="/" class="btn grey">Cancell</router-link>
                <div class="fixed-action-btn">

                </div>
            </form>
        </div>
    </div>
</template>

<script>
import Firebaseinit from '../firebaseinit';
    export default {
        name: 'edit',
        data() {
            return {
                 employee_id:null,
                 name: null,
                 dept: null ,
                 position :null
            }
        },
            beforeRouteEnter(to, from, next) {
            Firebaseinit.collection('1').where('employee_id', '==', to.params.employee_id).get().
            then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                            vm.employee_id = doc.data().employee_id,
                            vm.name = doc.data().name,
                            vm.dept = doc.data().dept,
                            vm.position = doc.data().position
                    })
                })
            })
        },
        watch: {
            '$route': 'theData'
        },
        methods: {
            theData() {
                Firebaseinit.collection('1').where('employee_id', '==', this.$route.params.employee_id).get().
                then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                            this.temployee_id = doc.data().employee_id,
                            this.name = doc.data().name,
                            this.dept = doc.data().dept,
                            this.position = doc.data().position
                    })
                })
            },
            udataEmployee(){
                  Firebaseinit.collection('1').where('employee_id', '==', this.$route.params.employee_id).get().
                    then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            temployee_id : this.employee_id,
                            name : this.name,
                            dept : this.dept,
                            position : this.position
                        })
                        .then(()=>{
                            this.$router.push({name:'view_employee', params:{id:this.employee_id}})
                        })    
                    })
                })
            }
    }
    }
</script>