<template>
  <nav>
    <div class="nav-wrapper">
      <div class="container">
        <router-link to="/" class="brand-logo center">Employee Manager</router-link>
        <ul class="right">
          <li class="item"><router-link to="/register">register</router-link></li>
          <li><router-link to="/Login">Login</router-link></li>
          <li><button @click="logout" class="btn black">Logout</button></li>
        </ul>
      </div>

    </div>
  </nav>

</template>

<script>
import firebase from 'firebase';
  export default {
    name: 'navbar',
    data() {
      return {
        IsLoggedIn:false,
        currentUser:false
      }
    },
    methods:{
      logout(){
        firebase.auth().signOut().then(() =>{
          this.$router.push('/login')
        })
      }
    }
  }
</script>

<style lang="scss">
  .nav-wrapper {
    background: #f39c12;
    font-weight: bold;
    .item{
      font-size: 18px;
      font-weight: bold;
    }
   
  }
</style>