<template>
    <div id="employee">
        <h2> New employee</h2>
        <div class="row">
            <form @submit.prevent="saveEmployee" class="col m8 s12">
                <div class="row">
                    <div class="input-field col m8 s12">
                        <input type="text" v-model="employee_id" placeholder="Employee ID#" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col m8 s12">
                        <input type="text" v-model="name" placeholder="Name" required>
                      
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col m8 s12">
                        <input type="text" v-model="dept" placeholder="Deptrment" required >
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col m8 s12">
                        <input type="text" v-model="position" placeholder="position" required >
                    
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancell</router-link>
                  <div class="fixed-action-btn">
                    
                </div>
            </form>
        </div>
    </div>
    
</template>

<script>
    import Firebaseinit from '../firebaseinit';

    export default {
        name: 'employee',
        data() {
            return {
                employee_id: null,
                name: null,
                dept: null,
                position: null,
            }
        },
        methods: {
            saveEmployee() {
                Firebaseinit.collection('1').add({
                    employee_id: this.employee_id,
                    name: this.name,
                    dept: this.dept,
                    position: this.position,

                }).then(docRef=>{
                    console.log(docRef)
                    this.$router.push('/')
                })
    
             
            }
        }
    }
</script>

